<template>
<div>
    <!--  -->
    <div id="add_project">
        <el-button type="primary" @click="new_product"></el-button>
        <el-input placeholder="" v-model="proname" style="width:200px"></el-input>
        <el-input placeholder="" v-model="prodes" style="width:200px"></el-input>
        <br><br>
    </div>
    <!--  -->
    <el-table
        stripe
        border
        :data="projects.filter(data => !search || data.proname.toLowerCase().includes(search.toLowerCase()) || data.prodes.toLowerCase().includes(search.toLowerCase()))"
        empty-text=""
        :header-cell-style="{background:'#ddd'}"
        highlight-current-row>
        <el-table-column label="" align="center" prop="proname" sortable>
        </el-table-column>
        <el-table-column label="" align="center" prop="prodes">
        </el-table-column>
        <el-table-column label="" align="center" prop="update_time" sortable>
            <template slot-scope="scope">
                <p>{{scope.row.update_time|dateFormat}}</p>
            </template>
        </el-table-column>
        <el-table-column label="" align="center" width="100">
            <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="" placement="top">
                    <a href="#" @click.prevent="go_webTest(scope.row)">
                        <p v-if="scope.row.webresult" style="color:green">PASS</p>
                        <p v-else style="color:red">FAIL</p>
                    </a>
                </el-tooltip>
            </template>
        </el-table-column>
        <el-table-column label="" align="center" width="100">
            <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="" placement="top">
                    <a href="#" @click.prevent="go_apiTest(scope.row)">
                        <p v-if="scope.row.apiresult" style="color:green">PASS</p>
                        <p v-else style="color:red">FAIL</p>
                    </a>
                </el-tooltip>
            </template>
        </el-table-column>
        <el-table-column label="" align="center" width="100">
            <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="" placement="top">
                    <a href="#" @click.prevent="go_appTest(scope.row)">
                        <p v-if="scope.row.appresult" style="color:green">PASS</p>
                        <p v-else style="color:red">FAIL</p>
                    </a>
                </el-tooltip>
            </template>
        </el-table-column>
        <el-table-column label="" align="center" width="100">
            <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="(+)" placement="top">
                    <a href="#" @click.prevent="projectTest(scope.row)">
                        <p v-if="scope.row.result" style="color:green" v-html="passText"></p>
                        <p v-else style="color:red" v-html="failText"></p>
                    </a>
                </el-tooltip>
            </template>
        </el-table-column>
        <el-table-column align="center" width="200px">
            <template slot="header">
                <el-input v-model="search" size="mini" placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="" placement="top">
                    <el-button
                        v-if='scope.row.user==userId || userId==2'
                        size="mini"
                        type="primary"
                        @click="open_edit(scope.row)" class="el-icon-edit">
                    </el-button>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="" placement="top">
                    <el-button
                        size="mini"
                        type="primary"
                        @click="go_report(scope.row)" icon="el-icon-document">
                    </el-button>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="" placement="top">
                    <el-button
                        v-if='scope.row.user==userId || userId==2'
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)" icon="el-icon-delete">
                    </el-button>
                </el-tooltip>
            </template>
        </el-table-column>
    </el-table>
    <br>
    <!--  -->
    <div style="text-align: center;">
        <el-button type="primary" :disabled="isPreDisabled" @click="get_pre"></el-button>
        <el-button type="primary" :disabled="isNextDisabled" @click="get_next"></el-button>
    </div>
    <!--  -->
    <el-dialog :visible.sync="dialogFormVisible">
        <el-form>
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.proname" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form >
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.prodes" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleEdit(editObj)"> </el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
/* eslint-disable */
import md5 from 'js-md5'
export default {
	name: "Project",
	data() {
		return {
            axios: this.axios,
            url: this.url,
            userId: this.storage.getItem('userId'),
            token: this.storage.getItem('token'),
            testUserId:this.storage.getItem('testUserId'),
            testUserToken:this.storage.getItem('testUserToken'),
            adminUserId:this.storage.getItem('adminUserId'),
            adminUserToken:this.storage.getItem('adminUserToken'),
            adminUser:'',
            adminPsw:'',
            testUser:'',
            testPsw:'',
            testBtn:'',
            loading:false,
            passText:'PASS',
            failText:'FAIL',
            proname: '',
            prodes: '',
            projects:[],
            search: '',
            pre:'',
            next:'',
            isNextDisabled:false,
            isPreDisabled:false,
            dialogFormVisible:false,
            editObj:{
                id:'',
                proname:'',
                prodes:'',
            }
		};
	},
	methods: {
        // 
        get_testUser() {
            // token
            var loginUrl = '/api/v1/user/login'
            var loginMethod = 'post'
            var baseurl = 'https://mpstest.longsys.com'
            // 
            var adminUserData = {
                'email':this.adminUser,
                'pswmd5':md5(this.adminPsw),
                'timestamp':Date.parse(new Date())
            }
            // 
            var testUserData = {
                'email':this.testUser,
                'pswmd5':md5(this.testPsw),
                'timestamp':Date.parse(new Date())
            }
            // token
            this.axios({
                baseURL:baseurl,
                url:loginUrl,
                method:loginMethod,
                data:adminUserData,
            }).then(response=>{
                if (response.data.errcode=='0') {
                    this.storage.setItem('adminUserId',response.data.data['userid'])
                    this.storage.setItem('adminUserToken',response.data.data['token'])
                    this.adminUserId=response.data.data['userid']
                    this.adminUserToken=response.data.data['token']
                    // token
                    this.axios({
                        baseURL:baseurl,
                        url:loginUrl,
                        method:loginMethod,
                        data:testUserData,
                    }).then(response=>{
                        if (response.data.errcode=='0') {
                            this.storage.setItem('testUserId',response.data.data['userid'])
                            this.storage.setItem('testUserToken',response.data.data['token'])
                            this.testUserId=this.storage.getItem('testUserId'),
                            this.testUserToken=this.storage.getItem('testUserToken')
                        }
                        else {
                            this.$message({
                                message: '',
                                type: 'error',
                                center: true,
                                showClose: true,
                                duration: 0
                            })
                            this.testUserFormVisible = true
                        }
                    },error=>{
                        this.$message({
                            message: ' ' + this.baseurl + ' ',
                            type: 'error',
                            center: true,
                            showClose: true,
                            duration: 0
                        })
                        return
                    })
                }
                else {
                    this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                        duration: 0
                    })
                    this.testUserFormVisible = true
                }
            },error=>{
                this.$message({
                    message: ' ' + this.baseurl + ' ',
                    type: 'error',
                    center: true,
                    showClose: true,
                    duration: 0
                })
                return
            })
        },
        // 
        projectTest(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true,
                        showClose: true
                    });
                    this.passText = this.failText = '<p class="el-icon-loading"></p>'
                    var params_data = {
                        'userId':this.userId,
                        'token':this.token,
                        'projectId':row['id']
                    }
                    var data = {}
                    if (row.proname == '') {
                        var data = {
                            testUserInfo:{
                                testUserId:this.testUserId,
                                testUserToken:this.testUserToken,
                                adminUserId:this.adminUserId,
                                adminUserToken:this.adminUserToken,
                            }
                        }
                    }
                    this.axios({
                        baseURL:this.url,
                        url:'/api/v1/projectTest/',
                        method:'post',
                        params:params_data,
                        data:data
                    }).then(response=>{
                        // 
                        if (!response.data.errcode) {
                            this.$message({
                                message: row['proname'] + '  PASS',
                                type: 'success',
                                center: true,
                                showClose: true,
                                duration:0,
                            });
                        }
                        else {
                            this.$message({
                                message: response.data.errmsg,
                                type: 'error',
                                center: true,
                                showClose: true,
                                duration:0,
                            })
                        }
                        this.passText='PASS'
                        this.failText='FAIL'
                        this.get_projects()
                    },error=>{
                        this.$message({
                            message: '',
                            type: 'error',
                            center: true,
                            showClose: true,
                            duration:0,
                        })
                        this.passText='PASS'
                        this.failText='FAIL'
                        this.testBtn=''
                    })
                }).catch(() => {
            });
        },
        // 
        get_projects() {
            var params_data = {'userId':this.userId,'token':this.token}
            this.axios({
                baseURL:this.url,
                url:'api/v1/project/',
                method:'get',
                params:params_data,
            }).then(response=>{
                this.projects=response.data.results
                // 
                this.pre=response.data.previous
                // 
                for (var i = 0, len = this.projects.length; i < len; i++) {
                    // console.log(this.projects)
                    if (this.projects[i].proname == '') {
                        this.adminUser=this.projects[i].adminUser,
                        this.adminPsw=this.projects[i].adminPsw,
                        this.testUser=this.projects[i].testUser,
                        this.testPsw=this.projects[i].testPsw
                        this.get_testUser()
                    }
                }
                if (!this.pre) {
                    this.isPreDisabled=true
                }
                else {
                    this.isPreDisabled=false
                }
                // 
                this.next=response.data.next
                if (!this.next) {
                    this.isNextDisabled=true
                }
                else {
                    this.isNextDisabled=false
                }
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
        // 
        open_edit(row) {
            this.dialogFormVisible = true
            // this.editObj=row
            this.editObj['id']=row.id
            this.editObj['proname']=row.proname
            this.editObj['prodes']=row.prodes
        },
        // 
        handleEdit(row) {
            if (!row.proname || !row.prodes) {
                this.$message({
                    message: "",
                    type: 'error',
                    center: true
                })
                return
            }
            // 
            this.dialogFormVisible = false
            var params_data = {'userId':this.userId,'token':this.token}
            this.axios({
                baseURL:this.url,
                url:'api/v1/project/'+row.id+'/',
                method:'patch',
                params:params_data,
                data:row,
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                    this.get_projects()
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                    message: '',
                    type: 'error',
                    center: true
                })
            })
        },
        // 
        handleDelete(index, row) {
            this.$confirm(', ?', '', {
                distinguishCancelAndClose: true,
                type: 'warning',
                confirmButtonText: '',
                cancelButtonText: ''
            }).then(() => {
                var params_data = {'userId':this.userId,'token':this.token}
                this.axios({
                    baseURL:this.url,
                    url:'api/v1/project/'+row.id+'/',
                    method:'delete',
                    params:params_data,
                }).then(response=>{
                    // 
                    if (!response.data.errcode) {
                        this.$message({
                            message: '',
                            type: 'success',
                            center: true
                        });
                    }
                    else {
                        this.$message({
                            message: "",
                            type: 'error',
                            center: true
                        })
                    }
                    this.get_projects()
                },error=>{
                    this.$message({
                        message: '',
                        type: 'error',
                        center: true
                    })
                })
            }).catch(action=>{
                // console.log(action)
            })
        },
        // 
        new_product() {
            if (!this.proname || !this.prodes) {
                this.$message({
                    message: "",
                    type: 'error',
                    center: true
                })
                return
            }
            var body_data = {
                    'proname': this.proname,
                    'prodes': this.prodes,
                    'user': this.userId,
                }
            var params_data = {'userId':this.userId,'token':this.token}
            this.axios({
                baseURL:this.url,
                url:'api/v1/project/',
                method:'post',
                params:params_data,
                data:body_data,
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                // 
                this.get_projects()
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                    message: '',
                    type: 'error',
                    center: true
                })
            })
            this.proname=''
            this.prodes=''
        },
        // 
        get_pre() {
            this.axios({
                url:this.pre,
                method:'get',
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.projects=response.data.results
                    // 
                    this.pre=response.data.previous
                    if (!this.pre) {
                        this.isPreDisabled=true
                    }
                    else {
                        this.isPreDisabled=false
                    }
                    // 
                    this.next=response.data.next
                    if (!this.next) {
                        this.isNextDisabled=true
                    }
                    else {
                        this.isNextDisabled=false
                    }
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                }
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
        // 
        get_next() {
            this.axios({
                url:this.next,
                method:'get',
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.projects=response.data.results
                    // 
                    this.pre=response.data.previous
                    if (!this.pre) {
                        this.isPreDisabled=true
                    }
                    else {
                        this.isPreDisabled=false
                    }
                    // 
                    this.next=response.data.next
                    if (!this.next) {
                        this.isNextDisabled=true
                    }
                    else {
                        this.isNextDisabled=false
                    }
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                }
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
        // 
        go_webTest(project) {
            var url = '/home/webManager/'
            var query = {
                'projectId': project.id,
                'projectName':project.proname
            }
            this.$router.push({ path: url,query:query})
        },
        // 
        go_apiTest(project) {
            var url = '/home/apiManager/'
            var query = {
                'projectId': project.id,
                'projectName':project.proname,
                'adminUser':project.adminUser,
                'adminPsw':project.adminPsw,
                'testUser':project.testUser,
                'testPsw':project.testPsw,
            }
            this.$router.push({ path: url, query:query})
        },
        // 
        go_appTest(project) {
            var url = '/home/appManager/'
            var query = {
                'projectId': project.id,
                'projectName':project.proname
            }
            this.$router.push({ path: url, query:query})
        },
        // 
        go_report(row) {
            var url = '/home/report/'
            var query = {
                projectName:row.proname
            }
            this.$router.push({ path: url, query:query})
        }
    },
    beforeCreate() {
    },
    created() {
        this.get_projects()
    },
    filters:{
        dateFormat:function(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            /* 00
            * 100   09:11:05
            * */
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 
            return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
        }
    }
};
</script>

<style scoped>
</style>



