<template>
<div>
    <el-collapse v-model="activeName" accordion>
        <el-collapse-item title="" name="1">
            <p>{{username}}</p>
            <p>&nbsp;&nbsp;&nbsp;&nbsp;{{email}}</p>
        </el-collapse-item>
        <el-collapse-item title="" name="2">
            <br>
            <el-input placeholder="" v-model="username_new" clearable>
            </el-input>
            <br><br>
            <el-button type="primary" @click="update_username"></el-button>
        </el-collapse-item>
        <el-collapse-item title="" name="3">
            <br>
            <el-input v-model="password_1" placeholder="" show-password clearable>
            <template slot="prepend"></template>
            </el-input>
            <br><br>
            <el-input v-model="password_2" placeholder="" show-password clearable>
            <template slot="prepend"></template>
            </el-input>
            <br><br>
            <el-button type="primary" @click="update_password"></el-button>
        </el-collapse-item>
    </el-collapse>
    <br><br>
    <el-button type="danger" @click="logout"></el-button>
</div>
</template>

<script>
/* eslint-disable */
var user;
export default {
    name: 'User',
    data() {
        return {
            axios: this.axios,
            url: this.url,
            userId: this.storage.getItem('userId'),
            token: this.storage.getItem('token'),
            username: '',
            username_new: '',
            email: '',
            password_1: '',
            password_2: '',
            activeName:'1'
        }
    },
    methods: {
        // 
        get_user() {
            var params_data = {'userId':this.userId,'token':this.token}
            this.axios({
                baseURL:this.url,
                url:'v1/user/'+this.userId+'/',
                method:'get',
                params:params_data,
            }).then(response=>{
                this.username = response.data.data.username
                this.email = response.data.data.email
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
        // 
        update_username() {
            var params_data = {'userId':this.userId,'token':this.token}
            var body_data = {'username': this.username_new}
            this.axios({
                baseURL:this.url,
                url:'v1/user/'+this.userId+'/',
                method:'patch',
                params:params_data,
                data:body_data,
            }).then(response=>{
                // errcode0
                if (!response.data.errcode) {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true,
                    });
                    this.username = this.username_new
                    this.username_new = ''
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true,
                    })
                }
            },error=>{
                this.$message({
                    message: '',
                    type: 'error',
                    center: true,
                })
            })
        },
        // 
        update_password() {
            if (this.password_1 && this.password_1==this.password_2) {
                var params_data = {'userId':this.userId,'token':this.token}
                var body_data = {'password': this.password_1}
                this.axios({
                    baseURL:this.url,
                    url:'v1/user/'+this.userId+'/',
                    method:'patch',
                    params:params_data,
                    data:body_data,
                }).then(response=>{
                    // 
                    if (!response.data.errcode) {
                        this.$message({
                            message: '',
                            type: 'success',
                            center: true
                        });
                    }
                    else {
                        this.$message({
                            message: "",
                            type: 'error',
                            center: true
                        })
                    }
                },error=>{
                    this.$message({
                        message: '',
                        type: 'error',
                        center: true
                    })
                })
            }
            else {
                this.$message({
                        message: "",
                        type: 'error',
                        center: true
                })
            }
            this.password_1 = this.password_2 = ''
        },
        // 
        logout() {
            this.storage.removeItem('userId')
            this.storage.removeItem('token')
            this.$router.push('/')
        }
    },
    beforeCreate() {
    },
    created() {
        this.get_user()
    }
}
</script>


<style scoped>
.el-collapse {
    width: 400px;
}
</style>




