<template>
<div>
    <!--  -->
    <a href="#" @click.prevent="go_back"><i class="el-icon-d-arrow-left"></i></a><br><br>
    <div style="font-size:17px;margin-bottom:10px">
            {{this.$route.query.projectName}}
    </div>
    <div style="font-size:17px;margin-bottom:10px">
            app{{this.$route.query.appName}}
    </div>
    <div style="font-size:17px">
            {{this.$route.query.appCase}}
    </div><br>
    <el-button type="primary" @click="appTest()" style="" :loading="loading">{{testBtn}}</el-button>
    <br><br>
    <el-collapse v-model="activeName" accordion>
        <el-collapse-item title="" name="1">
            <!-- app -->
            <div>
                <el-button type="primary" @click="new_appcase"></el-button>
                <el-input placeholder="" v-model="appname" style="width:200px"></el-input>
                <br><br>
            </div>
            <!-- appium -->
            <el-table
             border
             stripe
             :data="appCases.filter(data => (!search || data.appname.toLowerCase().includes(search.toLowerCase())))"
             empty-text=""
             :default-sort = "{prop: 'index', order: 'ascending'}"
             :header-cell-style="{background:'#ddd'}"
             highlight-current-row>
                <el-table-column label="" align="center" prop="index" width="50px" sortable>
                </el-table-column>
                <el-table-column label="" align="center" prop="appname">
                </el-table-column>
                <el-table-column label="" align="center" prop="selectmethod">
                </el-table-column>
                <el-table-column label="" align="center" prop="selectparam">
                </el-table-column>
                <el-table-column label="" align="center" prop="appoprate">
                </el-table-column>
                <el-table-column label="" align="center" prop="appparam">
                </el-table-column>
                <el-table-column label="" align="center">
                    <template slot-scope="scope">
                        <p>{{scope.row.update_time|dateFormat}}</p>
                    </template>
                </el-table-column>
                <el-table-column align="center">
                    <template slot="header">
                        <el-input v-model="search" size="mini" placeholder=""/>
                    </template>
                    <template slot-scope="scope">
                        <span v-if='!(scope.row.user==userId) && !(userId==2)'></span>
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button
                                v-if='scope.row.user==userId || userId==2'
                                size="mini"
                                type="primary"
                                @click="open_edit(scope.row)" class="el-icon-edit">
                            </el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button
                                v-if='scope.row.user==userId || userId==2'
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)" icon="el-icon-delete">
                            </el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <br>
            <!--  -->
            <div style="text-align: center;">
                <el-button type="primary" :disabled="isPreDisabled" @click="get_pre"></el-button>
                <el-button type="primary" :disabled="isNextDisabled" @click="get_next"></el-button>
            </div>
        </el-collapse-item>
        <el-collapse-item title="" name="2">
        <!--  -->
        <div>
            <el-button type="primary" @click="new_appcase('check')"></el-button>
            <el-input placeholder="" v-model="appname" style="width:200px"></el-input>
            <br><br>
        </div>
        <!-- app -->
        <el-table
            border
            stripe
            :data="checkAppCases.filter(data => (!search || data.appname.toLowerCase().includes(search.toLowerCase())))"
            empty-text=""
            :default-sort = "{prop: 'index', order: 'ascending'}"
            :header-cell-style="{background:'#ddd'}"
            highlight-current-row>
            <el-table-column label="" align="center" prop="index" width="50px" sortable>
            </el-table-column>
            <el-table-column label="" align="center" prop="appname">
            </el-table-column>
            <el-table-column label="" align="center" prop="selectmethod">
            </el-table-column>
            <el-table-column label="" align="center" prop="selectparam">
            </el-table-column>
            <el-table-column label="" align="center" prop="appoprate">
            </el-table-column>
            <el-table-column label="" align="center" prop="appparam">
            </el-table-column>
            <el-table-column label="" align="center" prop="checktext">
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <p>{{scope.row.update_time|dateFormat}}</p>
                </template>
            </el-table-column>
            <el-table-column align="center">
                <template slot="header">
                    <el-input v-model="search" size="mini" placeholder=""/>
                </template>
                <template slot-scope="scope">
                    <span v-if='!(scope.row.user==userId) && !(userId==2)'></span>
                    <el-tooltip class="item" effect="dark" content="" placement="top">
                        <el-button
                            v-if='scope.row.user==userId || userId==2'
                            size="mini"
                            type="primary"
                            @click="open_edit(scope.row,'check')" class="el-icon-edit">
                        </el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="" placement="top">
                        <el-button
                            v-if='scope.row.user==userId || userId==2'
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row, 'check')" icon="el-icon-delete">
                            </el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <br>
        <!--  -->
        <div style="text-align: center;">
            <el-button type="primary" :disabled="isCheckPreDisabled" @click="get_checkPre"></el-button>
            <el-button type="primary" :disabled="isCheckNextDisabled" @click="get_checkNext"></el-button>
        </div>
        </el-collapse-item>
    </el-collapse>
    <!--  -->
    <el-dialog :visible.sync="dialogFormVisible">
        <el-form>
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.appname" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form >
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.selectmethod" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form >
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.selectparam" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form >
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.appoprate" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form>
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.appparam" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form v-if="editObj.type">
            <el-form-item label="" label-width="120px">
            <el-input v-model="editObj.checktext" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <el-form >
            <el-form-item label="Index" label-width="120px">
            <!-- <el-input v-model="editObj.index" autocomplete="off"></el-input> -->
            <el-input-number v-model="editObj.index" controls-position="right"></el-input-number>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleEdit(editObj)"> </el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
/* eslint-disable */
var user;
export default {
    name:'AppCase',
    data() {
        return {
            axios: this.axios,
            url: this.url,
            userId: this.storage.getItem('userId'),
            token: this.storage.getItem('token'),
            testBtn:'',
            loading:false,
            appname: '',
            search: '',
            appCases: [],
            checkAppCases:[],
            pre:'',
            next:'',
            checkPre:'',
            checkNext:'',
            testType: this.$route.query.testType,
            isNextDisabled:false,
            isPreDisabled:false,
            isCheckNextDisabled:false,
            isCheckPreDisabled:false,
            dialogFormVisible:false,
            editObj:{
                id:'',
                appname:'',
                selectmethod:'',
                selectparam:'',
                appoprate:'',
                appparam:'',
                index:'',
                type:'',
                checktext:'',
            },
            activeName:''
        }
    },
    methods: {
        // 
        go_back() {
            this.$router.back(-1)
        },
        // app
        appTest() {
            this.$confirm('1 PC </br> \
                2 appium</br> \
                3  </br> \
                4  '+ this.$route.query.appCase + ' ', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                dangerouslyUseHTMLString: true
                }).then(() => {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                    this.loading=true
                    this.testBtn='...'
                    var params_data = {
                        'userId':this.userId,
                        'token':this.token,
                        'testType':this.testType
                    }
                    this.axios({
                        baseURL:this.url,
                        url:'/api/v1/appCaseTest/',
                        method:'get',
                        params:params_data,
                    }).then(response=>{
                        // 
                        if (!response.data.errcode) {
                            this.$message({
                                message: 'PASS',
                                type: 'success',
                                center: true,
                                showClose: true,
                                duration:0,
                            });
                        }
                        else {
                            this.$message({
                                message: response.data.errmsg,
                                type: 'error',
                                center: true,
                                showClose: true,
                                duration:0,
                            })
                        }
                        this.loading=false
                        this.testBtn=''
                    },error=>{
                        this.$message({
                            message: '',
                            type: 'error',
                            center: true,
                            showClose: true,
                            duration:0,
                        })
                        this.loading=false
                        this.testBtn=''
                    })
                }).catch(() => {        
            });
        },
        // 
        get_appCases() {
            var params_data = {
                'userId':this.userId,
                'token':this.token,
                'testType':this.testType
            }
            this.axios({
                baseURL:this.url,
                url:'api/v1/appCase/',
                method:'get',
                params:params_data,
            }).then(response=>{
                this.appCases=response.data.results
                // 
                this.pre=response.data.previous
                if (!this.pre) {
                    this.isPreDisabled=true
                }
                else {
                    this.isPreDisabled=false
                }
                // 
                this.next=response.data.next
                if (!this.next) {
                    this.isNextDisabled=true
                }
                else {
                    this.isNextDisabled=false
                }
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
        get_checkAppCases() {
            var params_data = {
                'userId':this.userId,
                'token':this.token,
                'testType':this.testType
            }
            this.axios({
                baseURL:this.url,
                url:'api/v1/checkAppCase/',
                method:'get',
                params:params_data,
            }).then(response=>{
                this.checkAppCases=response.data.results
                // 
                this.checkPre=response.data.previous
                if (!this.checkPre) {
                    this.isCheckPreDisabled=true
                }
                else {
                    this.isCheckPreDisabled=false
                }
                // 
                this.checkNext=response.data.next
                if (!this.checkNext) {
                    this.isCheckNextDisabled=true
                }
                else {
                    this.isCheckNextDisabled=false
                }
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
        // 
        open_edit(row, type) {
            this.dialogFormVisible = true
            // this.editObj = row
            this.editObj['id']=row.id
            this.editObj['appname']=row.appname
            this.editObj['selectmethod']=row.selectmethod
            this.editObj['selectparam']=row.selectparam
            this.editObj['appoprate']=row.appoprate
            this.editObj['appparam']=row.appparam
            this.editObj['index']=row.index
            this.editObj['type']=type
            if (type=='check') {
                this.editObj['checktext']=row.checktext
            }
        },
        // 
        handleEdit(row) {
            if (!row.appname && row.type!='check') {
                this.$message({
                    message: "",
                    type: 'error',
                    center: true
                })
                return
            }
            if (!row.appname && row.type=='check') {
                this.$message({
                    message: "",
                    type: 'error',
                    center: true
                })
                return
            }
            this.dialogFormVisible = false
            var params_data = {'userId':this.userId,'token':this.token}
            var url = 'api/v1/appCase/'+row.id+'/'
            if (row['type']=='check') {
                // 
                url = 'api/v1/checkAppCase/'+row.id+'/'
            }
            this.axios({
                baseURL:this.url,
                url:url,
                method:'patch',
                params:params_data,
                data:row
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                    this.get_appCases()
                    this.get_checkAppCases()
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                    message: '',
                    type: 'error',
                    center: true
                })
            })
        },
        // 
        handleDelete(index, row, type) {
            this.$confirm(', ?', '', {
                distinguishCancelAndClose: true,
                type: 'warning',
                confirmButtonText: '',
                cancelButtonText: '',
            }).then(() => {
                var params_data = {'userId':this.userId,'token':this.token}
                var url = 'api/v1/appCase/'+row.id+'/'
                if (type=='check') {
                    // 
                    url = 'api/v1/checkAppCase/'+row.id+'/'
                }
                this.axios({
                    baseURL:this.url,
                    url:url,
                    method:'delete',
                    params:params_data
                }).then(response=>{
                    // 
                    if (!response.data.errcode) {
                        this.$message({
                            message: '',
                            type: 'success',
                            center: true
                        });
                        this.get_appCases()
                        this.get_checkAppCases()
                    }
                    else {
                        this.$message({
                            message: "",
                            type: 'error',
                            center: true
                        })
                    }
                },error=>{
                   this.$message({
                        message: '',
                        type: 'error',
                        center: true
                    })
                })
            })
        },
        // 
        new_appcase(type) {
            if (!this.appname && type!='check') {
                this.$message({
                    message: "",
                    type: 'error',
                    center: true
                })
                return
            }
            if (!this.appname && type=='check') {
                this.$message({
                    message: "",
                    type: 'error',
                    center: true
                })
                return
            }
            var body_data = {
                'appname': this.appname,
                'testType': this.testType,
                'user':this.userId
            }
            var url = 'api/v1/appCase/'
            if (type=='check') {
                url = 'api/v1/checkAppCase/'
            }
            var params_data = {'userId':this.userId,'token':this.token}
            this.axios({
                baseURL:this.url,
                url:url,
                method:'post',
                params:params_data,
                data:body_data
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                    this.get_appCases()
                    this.get_checkAppCases()
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                    message: '',
                    type: 'error',
                    center: true
                })
            })
            this.appname=''
        },
        // 
        get_pre() {
            this.axios.get(this.pre).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.appCases=response.data.results
                    // 
                    this.pre=response.data.previous
                    if (!this.pre) {
                        this.isPreDisabled=true
                    }
                    else {
                        this.isPreDisabled=false
                    }
                    // 
                    this.next=response.data.next
                    if (!this.next) {
                        this.isNextDisabled=true
                    }
                    else {
                        this.isNextDisabled=false
                    }
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                }
            },error=>{
                this.$message({
                        message: error.response.data,
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
            })
        },
        get_checkPre() {
            this.axios.get(this.checkPre).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.checkAppCases=response.data.results
                    // 
                    this.checkPre=response.data.previous
                    if (!this.checkPre) {
                        this.isCheckPreDisabled=true
                    }
                    else {
                        this.isCheckPreDisabled=false
                    }
                    // 
                    this.checkNext=response.data.next
                    if (!this.checkNext) {
                        this.isCheckNextDisabled=true
                    }
                    else {
                        this.isCheckNextDisabled=false
                    }
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                }
            },error=>{
                this.$message({
                        message: error.response.data,
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
            })
        },
        // 
        get_next() {
            this.axios.get(this.next).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.appCases=response.data.results
                    // 
                    this.pre=response.data.previous
                    if (!this.pre) {
                        this.isPreDisabled=true
                    }
                    else {
                        this.isPreDisabled=false
                    }
                    // 
                    this.next=response.data.next
                    if (!this.next) {
                        this.isNextDisabled=true
                    }
                    else {
                        this.isNextDisabled=false
                    }
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                        message: error.response.data,
                        type: 'error',
                        center: true
                    })
            })
        },
        get_checkNext() {
            this.axios.get(this.checkNext).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.checkAppCases=response.data.results
                    // 
                    this.checkPre=response.data.previous
                    if (!this.checkPre) {
                        this.isCheckPreDisabled=true
                    }
                    else {
                        this.isCheckPreDisabled=false
                    }
                    // 
                    this.checkNext=response.data.next
                    if (!this.checkNext) {
                        this.isCheckNextDisabled=true
                    }
                    else {
                        this.isCheckNextDisabled=false
                    }
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                        message: error.response.data,
                        type: 'error',
                        center: true
                    })
            })
        },
    },
    beforeCreate() {
    },
    created() {
        // 
        this.get_appCases()
        this.get_checkAppCases()
    },
    filters:{
        dateFormat:function(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            /* 00
            * 100   09:11:05
            * */
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 
            return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
        }
    }
}
</script>

<style scoped>
</style>



